import React, { useState } from 'react'
import './index.scss'
/*
const colors = {
  payment: { true: '#12bc12', false: '#e50000' },
  delivery: { true: '#12bc12', false: '#e50000' }
} */

function Order({ data }) {
  const { number, date, sum, count, status} = data
  return (
    <div className="order">
      <div className="order__title">
          <div className="order__number">
              Заказ № {number} от {date}
          </div>
          <div className="order__info">
              {count} товар на сумму {sum} в статусе {status}
          </div>
      </div>
      <div className="order__content">
        <div className="order__row">
            <div className="order__rowTitle">
                <p>
                    Оплата
                </p>
            </div>
        </div>
        <div className="order__row content">
            <div className="order__column">
                <p className="order__text">
                    Счет № {number} от {date}, <span>оплата наличными</span>
                </p>
                <p className="order__text order__text-middle">
                    Сумма к оплате по счету: 5 090 руб.
                </p>
            </div>
            <div className="order__column">
                <p className="order__text order__text-small">
                    Не оплачено
                </p>
            </div>
        </div>
      </div>
      <div className="order__content">
        <div className="order__row">
            <div className="order__rowTitle">
                <p className="order__rowTitle">
                    Доставка
                </p>
            </div>
        </div>
        <div className="order__row content">
            <div className="order__column">
                <p className="order__text">
                    Счет № {number} от {date}, <span>оплата наличными</span>
                </p>
            </div>
            <div className="order__column">
                <p className="order__text order__text-small">
                    Не отгружен
                </p>
            </div>
        </div>
        <div className="order__row content">
            <div className="order__column">
                <p className="order__text">
                Статус отгрузки
                </p>
            </div>
            <div className="order__column">
                <p className="order__text order__text-small">
                ожидает обработки
                </p>
            </div>
        </div>
        <div className="order__row content">
            <div className="order__column">
                <p className="order__text">
                Служба доставки 
                </p>
            </div>
            <div className="order__column">
                <p className="order__text order__text-small">
                Москва и МО до 50км от МКАД
                </p>
            </div>
        </div>
      </div>
      <div className="order__content">
          <div className="order__row btn-row">
              <div className="order__column btn-column">
                  <a href="" className="btn btn-one">Подробнее о заказе</a>
              </div>
              <div className="order__column btn-column">
                  <a href="" className="btn btn-two">Повторить заказ</a>
                  <a className="btn btn-cancel">
                      Отменить заказ</a>
              </div>
          </div>
      </div>
    </div>
  )
}

export default Order
