import React, { useState } from 'react'
import { twelve } from '../../assets'

import { ReactComponent as PaymentIcon } from '../../assets/pay-day.svg'
import { NavLink } from 'react-router-dom'

import { Input, Button, Row, Col, Radio, Select, Icon } from 'antd'
import './index.scss'
function Order() {
  
  return (
    <div className="order__wrapper">
      <div className="order__row order__row-title">
        <div className="page-title">
            <h2>
                Отмена заказа
            </h2>
        </div>
        <a className="btn btn-one">
            Мои заказы
        </a>
      </div>
      <div className="order__row">
         <div>
         <p className="order__text order__text-big">
          Вы уверены, что хотите отменить  <a className="order__link" href="">заказ #356П</a>? <span className="accent-color">Отмена заказа необратима.</span> 
          </p>
          <p className="order__text order__text-middle">Укажите, пожалуйста, причину отмены заказа:</p>
         </div>
      </div>
      <div className="order_row">
          <textarea name="" id="" cols="30" rows="10" className="order__textarea" placeholder="Опишите причины отказа">
              
          </textarea>
      </div>
      <div className="order__row">
          <a className="btn btn-cancel">
              Отменить заказ
          </a>
      </div>
    </div>
  )
}

export default Order
