import React, { useState } from 'react'
import {
  Row,
  Col,
  Input,
  Button,
  Menu,
  Dropdown,
  Icon,
  Checkbox,
  Drawer
} from 'antd'
import { NavLink } from 'react-router-dom'
import OrderTable from '../../components/OrderTable'
import OrderEmpty from '../../components/OrderEmpty'
import OrderCancel from '../../components/OrderCancel'
import './index.scss'

const orders = [
    {
        number: '№357П',
        date: '02.07.2019',
        sum: '5 090 руб.',
        count: '1',
        status: '«Принят, ожидается оплата»: заказ находится в очереди на обработку менеджером',
    },
    {
        number: '№357П',
        date: '02.07.2019',
        sum: '5 090 руб.',
        count: '1',
        status: '«Принят, ожидается оплата»: заказ находится в очереди на обработку менеджером',
    }
]
function Order(){
    const [empty, setEmpty] = useState(false);
    const [cancel, setCancel] = useState(false)
    return (
        <div>
        {empty ? (
            <OrderEmpty />
      ) : cancel ? (
          <OrderCancel />
      ) : (
        <>
            <div className="checkout">
                <div className="checkout__row">
                    <div className="checkout__title page-title">
                        <h2>
                           Мои заказы
                        </h2>
                    </div>
                    <a className="btn btn-one">
                        История заказов
                    </a>
                </div>
            </div>
            <div className="order-table">
            {orders.map((order) => (
                <OrderTable data={order} />
            ))}
            </div>
            <NavLink to="/user-profile/order">
                <a className="btn btn-one"
                  onClick={() => {
                    setEmpty(true)
                  }}
                >
                  Пустые
                </a>
              </NavLink>
              <NavLink to="/user-profile/order">
                <a className="btn btn-cancel"
                  onClick={() => {
                    setCancel(true)
                  }}
                
                >
                  Отмена
                </a>
              </NavLink>
        </>
        )}
        </div>
      )
}
export default Order